import React from 'react';
import Typography from '@material-ui/core/Typography';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Pagination from '@material-ui/lab/Pagination';
import StorefrontIcon from '@material-ui/icons/Storefront';
import {StoicIdentity} from '../ic/identity.js';
import extjs from '../ic/extjs.js';
import Alert from '@material-ui/lab/Alert';
import { useTheme } from '@material-ui/core/styles';
import ListingBuyForm from '../components/ListingBuyForm';
import { useSelector } from 'react-redux'
import Listing from '../components/Listing';
const perPage = 30;
const api = extjs.connect("https://boundary.ic0.app/");
const collections = [
  {
    canister : "e3izy-jiaaa-aaaah-qacbq-cai",
    name : "Cronic Critters",
    comaddress : "c7e461041c0c5800a56b64bb7cefc247abc0bbbb99bd46ff71c64e92d9f5c2f9",
  },
  {
    canister : "nbg4r-saaaa-aaaah-qap7a-cai",
    name : "Starverse",
    comaddress : "c7e461041c0c5800a56b64bb7cefc247abc0bbbb99bd46ff71c64e92d9f5c2f9",
  },
  {
    canister : "bxdf4-baaaa-aaaah-qaruq-cai",
    name : "Wrapped ICPunks",
    comaddress : "38bc2ce470085db3a3223806e61946f645106915a0a7da8fa9368969db7a3264",
  },
  {
    canister : "uzhxd-ziaaa-aaaah-qanaq-cai",
    name : "ICP News",
    comaddress : "c7e461041c0c5800a56b64bb7cefc247abc0bbbb99bd46ff71c64e92d9f5c2f9",
  },
  {
    canister : "tde7l-3qaaa-aaaah-qansa-cai",
    name : "Cronic Wearables",
    comaddress : "c7e461041c0c5800a56b64bb7cefc247abc0bbbb99bd46ff71c64e92d9f5c2f9",
  },
  {
    canister : "owuqd-dyaaa-aaaah-qapxq-cai",
    name : "ICPuzzle",
    comaddress : "c7e461041c0c5800a56b64bb7cefc247abc0bbbb99bd46ff71c64e92d9f5c2f9",
  },
  {
    canister : "gevsk-tqaaa-aaaah-qaoca-cai",
    name : "ICmojis",
    comaddress : "df13f7ef228d7213c452edc3e52854bc17dd4189dfc0468d8cb77403e52b5a69",
  },
  {
    canister : "gevsk-tqaaa-aaaah-qaoca-cai",
    name : "ICmojis",
    comaddress : "df13f7ef228d7213c452edc3e52854bc17dd4189dfc0468d8cb77403e52b5a69",
  },
];
var cb = null;
const txfee = 10000;
const txmin = 100000;
const txcomm = 0.015;
var intv = false;
export default function Marketplace(props) {
  const currentPrincipal = useSelector(state => state.currentPrincipal);
  const identity = useSelector(state => (state.principals.length ? state.principals[currentPrincipal].identity : {}));
  const [listings, setListings] = React.useState([]);
  const accounts = useSelector(state => state.principals[currentPrincipal].accounts)
  const [page, setPage] = React.useState(1);
  const [listingBuyFormOpen, setListingBuyFormOpen] = React.useState(false);
  const [sort, setSort] = React.useState('price_asc');

  const handleChange = (event) => {
    setSort(event.target.value);
  };
  
  const listingBuyFormSubmit = (d) => {
    listingBuyFormClose();
    cb(d);
    cb = null;
  }  
  
  
  const listingBuyFormClose = () => {    
    setListingBuyFormOpen(false);
  };
  const showListingBuyForm = () => {
    setListingBuyFormOpen(true);
    return new Promise((resolve, reject) => {
      cb = resolve;
    });
  };
  
  const theme = useTheme();
  const styles = {
    root : {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
    empty : {
      maxWidth:800,
      margin : "0 auto",
      textAlign:"center"
    },
    grid: {
      flexGrow: 1,
      padding: theme.spacing(2)
    },

    largeIcon: {
      width: 60,
      height: 60,
    },
  };
  const _process = async () => {
    props.loader(true);
    await processPayments();
    await processRefunds();
    props.loader(false);
  };
  const _updates = async () => {
    console.log("poll");
    refreshListings();
    // await processPayments();
    // await processRefunds();
    setTimeout(_updates, 30 *1000);
  };
  
  const processPayments = async () => {
    const id = StoicIdentity.getIdentity(identity.principal);
    const _api = extjs.connect("https://boundary.ic0.app/", id);
    for(var j = 0; j < collections.length; j++){
      var payments = await _api.canister(collections[j].canister).payments();
      if (payments.length === 0) continue;
      if (payments[0].length === 0) continue;
      console.log("Payments found: " + payments[0].length);
      var a, b, c, payment;
      for(var i = 0; i < payments[0].length; i++) {
        payment = payments[0][i];
        a = extjs.toAddress(identity.principal, payment);
        b = Number(await api.token().getBalance(a));
        c = Math.round(b * txcomm);
        try {
          if (b > txmin) {
            await _api.token().transfer(identity.principal, payment, accounts[0].address, BigInt(b-(txfee + c)), BigInt(txfee));
            await _api.token().transfer(identity.principal, payment, collections[j].comaddress, BigInt(c-txfee), BigInt(txfee));
          }
          await _api.canister(collections[j].canister).removePayments([payment]);
          console.log("Payments removed successfully");
        } catch (e) {};
      };
    };
  };
  const processRefunds = async () => {
    const id = StoicIdentity.getIdentity(identity.principal);
    const _api = extjs.connect("https://boundary.ic0.app/", id);
    for(var j = 0; j < collections.length; j++){
      var refunds = await _api.canister(collections[j].canister).refunds();
      if (refunds.length === 0) continue;
      if (refunds[0].length === 0) continue;
      var a, b, refund;
      for(var i = 0; i < refunds[0].length; i++) {
        refund = refunds[0][i];
        a = extjs.toAddress(identity.principal, refund);
        b = Number(await api.token().getBalance(a));
        try {
          if (b > txfee) {
            //Process refunds
            await _api.token().transfer(identity.principal, refund, "07ce335b2451bec20426497d97afb0352d89dc3f1286bf26909ecb90cf370c76", BigInt(b-txfee), BigInt(txfee));
          }
          await _api.canister(collections[j].canister).removeRefunds([refund]);
          console.log("Refund removed successfully");
        } catch (e) {};
      };
    };
  };
  
  const refreshListings = () => {
    api.canister("e3izy-jiaaa-aaaah-qacbq-cai").listings().then(listings => {
      setListings(listings);
    }).finally(() => {
      props.loader(false);      
    });
  }
  const error = (e) => {
    props.alert("There was an error", e);
  };
  React.useEffect(() => {
    props.loader(true);
    
    _updates();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
    
  return (
    <div style={styles.root}>
    <Alert severity="warning">If you have not received ICP for sold NFTs through the marketplace you can trigger a manual withdraw here <Button onClick={_process} variant="contained" size={"small"} color={"primary"}>Withdraw ICP</Button></Alert>
    {listings.length === 0 ?
      <div style={styles.empty}>
        <Typography paragraph align="center">
          <StorefrontIcon style={styles.largeIcon} />
        </Typography>
        <Typography paragraph style={{paddingTop:20,fontWeight:"bold"}} align="center">
          There are no listings added to the marketplace. You can list NFTs for sale directly from your wallet! 
          
        </Typography>
        <Button onClick={() => {props.loader(true);refreshListings();}} variant="contained" color="primary">Refresh Listings</Button>
      </div> :
      <>
        <div style={styles.empty}>
          <Typography paragraph style={{paddingTop:20,fontWeight:"bold"}} align="center">
            Buy and Sell NFTs from the Marketplace. You can list NFTs for sale directly from your wallet!
          </Typography>
          <Button onClick={() => {props.loader(true);refreshListings();}} variant="contained" color="primary">Refresh Listings</Button>
          
        </div>
        <div style={{marginLeft:"20px",marginTop:"10px"}}>
          <FormControl >
            <Select
              value={sort}
              onChange={handleChange}
            >
              <MenuItem value={"price_asc"}>Price Ascending</MenuItem>
              <MenuItem value={"price_desc"}>Price Descending</MenuItem>
              <MenuItem value={"mint_number"}>Minting #</MenuItem>
              <MenuItem value={"type"}>Rare Type</MenuItem>
              <MenuItem value={"gri"}>Genetic Rarity Index</MenuItem>
            </Select>
          </FormControl>
          
          {listings.length > perPage ?
          <Pagination style={{float:"right",marginTop:"5px",marginBottom:"20px"}} size="small" count={Math.ceil(listings.length/perPage)} page={page} onChange={(e, v) => setPage(v)} />
          : ""}
        </div>
        <div style={styles.grid}>
          <Grid
            container
            spacing={2}
            direction="row"
            justifyContent="flex-start"
            alignItems="flex-start"
          >
            {listings.slice().sort((a,b) => {
              switch(sort) {
                case "price_asc":
                  return Number(a[1].price)-Number(b[1].price);
                case "price_desc":
                  return Number(b[1].price)-Number(a[1].price);
                case "gri":
                  return (Number(gridata[b[0]])*100)-(Number(gridata[a[0]])*100);
                case "mint_number":
                  return a[0]-b[0];
                case "type":
                  var _a = a[2].nonfungible.metadata[0][30]%41;
                  var _b = b[2].nonfungible.metadata[0][30]%41;
                  if (_a === 2) _a = 1;
                  if (_a > 1) _a = 2;
                  if (_b === 2) _b = 1;
                  if (_b > 1) _b = 2;
                  var d = _a-_b;
                  if (d === 0) {
                    if (Number(a[1].price)>Number(b[1].price)) return 1;
                    if (Number(a[1].price)<Number(b[1].price)) return -1;
                  };
                  return d;
                default:
                  return 0;
              };
            }).filter((token,i) => (i >= ((page-1)*perPage) && i < ((page)*perPage))).map((listing, i) => {
              return (<Listing gri={gridata[listing[0]]} refreshListings={refreshListings} showListingBuyForm={showListingBuyForm} loader={props.loader} alert={props.alert} error={error} key={listing[0]} listing={listing} confirm={props.confirm} />)
            })}
          </Grid>
        </div>
        {listings.length > perPage ?
          <Pagination style={{float:"right",marginTop:"5px",marginBottom:"20px"}} size="small" count={Math.ceil(listings.length/perPage)} page={page} onChange={(e, v) => setPage(v)} />
          : ""}
      </>
    }
    <ListingBuyForm open={listingBuyFormOpen} onSubmit={listingBuyFormSubmit} onClose={listingBuyFormClose} />
    </div>
  );
}

